<template>
  <div>
    <div class="container-fluid">
      <form @submit.prevent="search">
        <div class="row align-items-end">
          <div class="col-md-4">
            <PlaceSelector
              :label="t('label.from')"
              v-model="model.from"
              :places="fromList"
            />
          </div>

          <div class="col-md-auto">
            <button
              type="button"
              class="btn btn-outline-secondary mb-3"
              @click="swapPlaces"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="currentColor"
                class="bi bi-arrow-left-right"
                viewBox="0 0 16 16"
              >
                <path
                  fill-rule="evenodd"
                  d="M1 11.5a.5.5 0 0 0 .5.5h11.793l-3.147 3.146a.5.5 0 0 0 .708.708l4-4a.5.5 0 0 0 0-.708l-4-4a.5.5 0 0 0-.708.708L13.293 11H1.5a.5.5 0 0 0-.5.5zm14-7a.5.5 0 0 1-.5.5H2.707l3.147 3.146a.5.5 0 1 1-.708.708l-4-4a.5.5 0 0 1 0-.708l4-4a.5.5 0 1 1 .708.708L2.707 4H14.5a.5.5 0 0 1 .5.5z"
                ></path>
              </svg>
            </button>
          </div>

          <div class="col-md-4">
            <PlaceSelector
              :label="t('label.to')"
              v-model="model.to"
              :places="toList"
            />
          </div>

          <div class="col-md-2">
            <div>
              <label for="formInputDate" class="form-label">{{
                t("label.when")
              }}</label>
              <Datepicker
                id="formInputDay"
                v-model="model.date"
                class="mb-3"
                :minDate="new Date()"
                :enableTimePicker="false"
                :autoApply="true"
                required
              />
            </div>
          </div>

          <div class="col">
            <div class="d-grid">
              <button
                :disabled="!formValid"
                type="submit"
                class="btn btn-primary mb-3"
              >
                {{ t("label.search") }}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-search"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </form>

      <div class="row my-3">
        <div class="col-12">
          <TimetableList :results="results" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useI18n } from "vue-i18n";
import Datepicker from "@vuepic/vue-datepicker";
import TimetableList from "@/components/TimetableList.vue";
import PlaceSelector from "@/components/PlaceSelector.vue";

import places from "@/data/places.json";
import timetables from "@/data/timetables.json";
import providersMap from "@/data/providers.json";

export default {
  components: { Datepicker, TimetableList, PlaceSelector },
  data() {
    return {
      places,
      timetables,

      model: {
        from: undefined,
        to: undefined,
        date: undefined,
      },
      results: [],
    };
  },
  setup() {
    const { t } = useI18n(); // call `useI18n`, and spread `t` from  `useI18n` returning

    return { t }; // return render context that included `t`
  },
  computed: {
    formValid() {
      return !!this.model.from && !!this.model.to && !!this.model.date;
    },
    fromList() {
      return this.places.filter(({ id }) => id !== this.model.to?.id);
    },
    toList() {
      return this.places.filter(({ id }) => id !== this.model.from?.id);
    },
  },
  methods: {
    search() {
      const today = new Date().getTime();

      const { from, to } = this.model;
      const day = this.model.date.getDay();

      this.results = timetables
        // date range filter
        .filter(({ fromDate, toDate }) => {
          return new Date(fromDate) <= today && today <= new Date(toDate);
        })
        .reduce((acc, { timetable, providerId }) => {
          acc.push(
            ...timetable.map((item) => ({
              ...item,
              providerId,
            }))
          );

          return acc;
        }, [])

        // day filter
        .filter(
          (timetable) =>
            timetable.from === from.id &&
            timetable.to === to.id &&
            timetable.days.includes(day)
        )
        .reduce((acc, { hours, informations, providerId }) => {
          acc.push(
            ...hours.map((hour) => ({
              from: from.name,
              to: to.name,
              hour,
              informations: informations?.[hour],
              provider: providersMap[providerId],
            }))
          );

          return acc;
        }, [])

        // sort result by time
        .sort(({ hour: a }, { hour: b }) => (a > b ? 1 : a < b ? -1 : 0));
    },
    swapPlaces() {
      const { from, to } = this.model;

      this.model.from = to;
      this.model.to = from;
    },
  },
};
</script>
